<template>
  <img alt="Vue logo" src="./assets/6439010068.jpg" />
  <h1>{{ firstname }} {{ lastname }}</h1>
  <button v-on:click="showalertfn">แสดงชื่อ</button>
  <button @click="showalertln">แสดงนามสกุล</button>
  <h1>{{ age }}</h1>
  <button @click="plus">เพิ่มตัวเลข</button>
  <button @click="del">ลดตัวเลข</button><br />
  <button @click="plus5">เพิ่มตัวเลขที่ละ 5</button>
  <button @click="del5">ลดตัวเลขที่ละ 5</button>
  <form actin="">
    <label for="">กรุณากรอกชื่อจริง</label>
    <input type="text" v-on:input="setfirstname" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกชื่อนามสกุล</label>
    <input type="text" v-on:input="setlastname" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกชื่อเล่น</label>
    <input type="text" v-on:input="setnickname" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกอายุ</label>
    <input type="text" v-on:input="setage" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกวัน/เดือน/ปีเกิด</label>
    <input type="text" v-on:input="setbirth" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกสาขา</label>
    <input type="text" v-on:input="setbranch" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกห้อง</label>
    <input type="text" v-on:input="setroom" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกคติประจำใจ</label>
    <input type="text" v-on:input="setmotto" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกสถานที่เรียนต่อ</label>
    <input type="text" v-on:input="setstudyplace" />
  </form>
  <form actin="">
    <label for="">กรุณากรอกวิชาที่ไม่ชอบ</label>
    <input type="text" v-on:input="setsubjectdontlike" />
  </form>
  <h2>แสดงชื่อจริง : {{ showfirstname }}</h2>
  <h2>แสดงชื่อนามสกุล : {{ showlastname }}</h2>
  <h2>แสดงชื่อเล่น : {{ shownickname }}</h2>
  <h2>แสดงอายุ : {{ showage }}</h2>
  <h2>แสดงวัน/เดือน/ปีเกิด : {{ showbirth }}</h2>
  <h2>แสดงสาขา : {{ showbranch }}</h2>
  <h2>แสดงห้อง :{{ showroom }}</h2>
  <h2>แสดงคติประจำใจ :{{ showmotto }}</h2>
  <h2>แสดงสถานที่เรียนต่อ :{{ showstudyplace }}</h2>
  <h2>แสดงวิชาที่ไม่ชอบ :{{ showsubjectdontlike }}</h2>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      firstname: "Sarawut",
      lastname: "Sriprajan",
      age: 20,
      showfirstname: "",
      showlastname: "",
      shownickname: "",
      showage: "",
      showbirth: "",
      showbranch: "",
      showroom: "",
      showmotto: "",
      showstudyplace: "",
      showsubjectdontlike: "",
    };
  },
  methods: {
    showalertfn() {
      alert(this.firstname);
    },
    showalertln() {
      alert(this.lastname);
    },
    plus() {
      this.age++;
    },
    del() {
      this.age--;
    },
    plus5() {
      this.age += 5;
    },
    del5() {
      this.age -= 5;
    },
    setfirstname(event) {
      // console.log(event.target.value);
      this.showfirstname = event.target.value;
    },
    setlastname(event) {
      this.showlastname = event.target.value;
    },
    setnickname(event) {
      this.shownickname = event.target.value;
    },
    setage(event) {
      this.showage = event.target.value;
    },
    setbirth(event) {
      this.showbirth = event.target.value;
    },
    setbranch(event) {
      this.showbranch = event.target.value;
    },
    setroom(event) {
      this.showroom = event.target.value;
    },
    setmotto(event) {
      this.showmotto = event.target.value;
    },
    setstudyplace(event) {
      this.showstudyplace = event.target.value;
    },
    setsubjectdontlike(event) {
      this.showsubjectdontlike = event.target.value;
    },
  },
};
</script>

